.footer {
  background: #252734;
  color: #fff;
  padding-top: 34px;
  padding-bottom: 56px;
  @media (width < 680px) {
    padding-bottom: 20px;
  }
  &_container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    @media (width < 680px) {
      display: block;
      text-align: center;
      padding: 25px;
    }
  }

  &_logo {
    width: 250px;
    flex-shrink: 0;
    svg {
      max-width: 100%;
      use {
        fill: white;
      }
    }
    @media (width < 680px) {
      width: 100%;
      margin-bottom: 54px;
      img {
        width: 170px;
      }
    }
  }
  &_copy {
    margin-right: 103px;
    @media (width < 1150px) {
      margin-right: 57px;
    }
    @media (width < 1000px) {
      max-width: 160px;
      margin-right: 90px;
    }
    @media (width < 680px) {
      max-width: 100% !important;
      margin-right: 0 !important;
    }
  }
  &_phone {
    color: #fff;
    font-weight: 300;
    font-size: 22px;
    line-height: 32px;
    margin-bottom: 20px;
    @media (width < 680px) {
      font-weight: 300;
      font-size: 22px;
      line-height: 32px;
    }
  }
  &_social {
    display: flex;
    margin-bottom: 60px;
    gap: 16px;
    @media (width < 680px) {
      display: none;
    }
    svg {
      height: 24px;
      width: 24px;
    }
    svg use {
      fill: white;
    }
    a:hover {
      svg use {
        fill: $light-blue;
      }
    }
  }
  &_bottom {
    font-size: 10px;
    line-height: 15px;
    padding-top: 27px;
  }
  &_dev {
    margin-left: auto;
    text-align: right;
    display: flex;
    align-items: baseline;
    justify-content: flex-end;
    gap: 10px;
    @media (width < 680px) {
      display: block;
      text-align: left;
      margin-top: 30px;
    }
    svg {
      height: 20px;
      width: 30px;
    }
    svg use {
      fill: white;
    }
    a:hover {
      svg use {
        fill: $light-blue;
      }
    }
  }
  &_nav {
    list-style: none;
    font-weight: 700;
    font-size: 14px;
    line-height: 250%;
    text-transform: uppercase;
    @media (width < 680px) {
      margin-top: 30px;
    }
  }
  &_catalogue {
    font-weight: 700;
    font-size: 14px;
    line-height: 250%;
    @media (width < 980px) {
      display: none;
    }
    ul {
      list-style: none;
      font-weight: 400;
    }
  }
  &_address {
    max-width: 295px;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    margin-bottom: 20px;
    @media (width < 1000px) {
      max-width: 100%;
      font-weight: 400;
      font-size: 12px;
      line-height: 22px;
    }
    strong {
      text-transform: uppercase;
    }
  }
  a {
    color: #fff;
    text-decoration: none;
    &:hover {
      color: $light-blue;
    }
  }
  .tg_link {
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;
    display: block;
    margin-top: 43px;
    @media (width < 680px) {
      font-size: 12px;
      line-height: 18px;
      margin-top: 0;
      display: inline-block;
    }
    svg {
      width: 18px;
      height: 15px;
      margin-right: 15px;
      float: left;
      vertical-align: bottom;
    }
    span {
      color: $light-blue;
    }
  }
  .map_link {
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.3em;
    text-transform: uppercase;
    color: $light-blue;
    @media (width < 680px) {
      display: none;
    }
    svg {
      width: 16px;
      height: 20px;
      margin-right: 15px;
    }
  }
}
